// {"title":"AngleSharp v0.9.2 released","date":"2015-09-24 23:44:00","categories":"news release","author":"Florian Rappl","userId":"FlorianRappl","section":"Posts","link":"/blog/2015-09-24-anglesharp-v092-released","source":"/home/runner/work/anglesharp.github.io/anglesharp.github.io/docs/blog/2015-09-24-anglesharp-v092-released.md"}
import * as React from 'react';
import { ContentPage } from 'piral-docs-tools/components';

      import { PageContent, Markdown, getPageLayout } from 'piral-docs-tools/components';

      const PageLayout = getPageLayout("blog");
      const link = "https://github.com/AngleSharp/anglesharp.github.io/tree/main/docs/blog/2015-09-24-anglesharp-v092-released.md";
      const html = `
<p>This week's minor update was only a small patch that fixed a bug in the tokenizer and improved the XML parser's performance. It also features the brandnew <code>application/json</code> encoding type for form submission. The form submission process internals have been redesigned to be much easier to extend and use. The <code>FormDataSet</code> and <code>FormDataSetEntry</code> classes are now public. This forms the basis for sending forms without requiring a webpage or valid <code>&lt;form&gt;</code> element at all.</p>
<p>The renewed CSSOM is not part of v0.9.2. Also the previously mentioned update to the service model and the event aggregator change could not make it. They will (at least partially, hopefully) find their way into v0.9.3 or some update afterwards.</p>
<p>Until this point more and more updates are being done via feature branches. From now on this will be the only model. Every new feature or update has to be merged to <code>devel</code> from a specific feature branch, which is associated with an issue. The idea is to make the development as transparent and open as possible. Also future contributions, discussions, and general user engagement should be boosted.</p>
`;
